<template>
  <div>
    <gmap-map
      id="map"
      :center="center"
      :zoom="zoomMapa"
      :options="options"
      map-type-id="hybrid"
    >
      <gmap-marker :position="center"> </gmap-marker>

      <gmap-polyline
        v-bind:path.sync="path"
        v-bind:options="{ strokeColor: '#008000' }"
      >
      </gmap-polyline>

      <gmap-marker
        :key="index"
        v-for="(m, index) in markers"
        :position="m.position"
        :aria-label="index"
        @click="toggleInfoWindow(m, index)"
        icon="http:\\maps.google.com/mapfiles/ms/icons/orange-dot.png"
      >
      </gmap-marker>
    </gmap-map>

    <div id="floating-panel">
      <div class="row">
        <div class="col-md-5">
          <select id="machines" class="form-control">
          </select>
        </div>

        <div class="col-md-4">
          <date-picker
            id="dateTrayectos"
            v-model="startDateTrayecto"
            valueType="format"
            format="YYYY-MM-DD"
            style="margin-top: 0.25rem;"
          ></date-picker>
        </div>

        <!-- <div class="col-md-3">
          <date-picker v-model="endDate" valueType="format" format="DD/MM/YYYY"></date-picker>
        </div> -->

        <div class="col-md-2">
          <button
            id="searchTrayectos"
            class="btn btn-primary"
            @click="obtainDataHistoricos()"
            style="margin-left: 2rem; margin-right: 3rem;"
          >
            Buscar
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<style>
#floating-panel {
  position: absolute;
  top: 4rem;
  left: 1%;
  z-index: 5;
  background-color: #fff;
  padding: 5px;
  border: 1px solid #999;
  text-align: center;
  font-family: "Roboto", "sans-serif";
  line-height: 30px;
  padding-left: 10px;
  /* display: inline-flex; */
  width: auto !important;
}

#floating-panel .col-md-3 .mx-datepicker {
  display: contents;
}
</style>

<script>
import { API_KEY } from "./Maps/API_KEY";
import Vue from "vue";
import * as VueGoogleMaps from "vue2-google-maps";
import DatePicker from "vue2-datepicker";
import "vue2-datepicker/index.css";

import api, { today } from "./apiData/getApiData";

Vue.use(VueGoogleMaps, {
  load: {
    key: API_KEY
  }
});

export default {
  components: {
    DatePicker
  },
  data() {
    return {
      startDateTrayecto: today,
      endDate: today,

      zoomMapa: 6,

      center: { lat: 20.674375, lng: -103.387843 }, // Coordenadas de la Minerva en Guadalajara
      path: null,
      markers: null,
      options: {
        styles: [
          {
            featureType: "water",
            stylers: [
              { saturation: 43 },
              { lightness: -11 },
              { hue: "#0088ff" }
            ]
          },
          {
            featureType: "road",
            elementType: "geometry.fill",
            stylers: [
              { hue: "#ff0000" },
              { saturation: -100 },
              { lightness: 99 }
            ]
          },
          {
            featureType: "road",
            elementType: "geometry.stroke",
            stylers: [{ color: "#808080" }, { lightness: 54 }]
          },
          {
            featureType: "landscape.man_made",
            elementType: "geometry.fill",
            stylers: [{ color: "#ece2d9" }]
          },
          {
            featureType: "poi.park",
            elementType: "geometry.fill",
            stylers: [{ color: "#ccdca1" }]
          },
          {
            featureType: "road",
            elementType: "labels.text.fill",
            stylers: [{ color: "#767676" }]
          },
          {
            featureType: "road",
            elementType: "labels.text.stroke",
            stylers: [{ color: "#ffffff" }]
          },
          { featureType: "poi", stylers: [{ visibility: "off" }] },
          {
            featureType: "landscape.natural",
            elementType: "geometry.fill",
            stylers: [{ visibility: "on" }, { color: "#b8cb93" }]
          },
          { featureType: "poi.park", stylers: [{ visibility: "on" }] },
          {
            featureType: "poi.sports_complex",
            stylers: [{ visibility: "on" }]
          },
          { featureType: "poi.medical", stylers: [{ visibility: "on" }] },
          {
            featureType: "poi.business",
            stylers: [{ visibility: "simplified" }]
          }
        ]
      }
    };
  },
  methods: {
    async obtainDataHistoricos() {

      let dataCoordinates = JSON.parse(await api.getDataTrayecto());
      let totDataCoord = dataCoordinates.length - 1;

      this.path = dataCoordinates[0];
      this.center = {lat: dataCoordinates[0][totDataCoord].lat, lng: dataCoordinates[0][totDataCoord].lng};
      this.zoomMapa = 12;

      // intevalAdvance
      // this.center = {lat: dataCoordinates[0][0].latitud, lng: dataCoordinates[0][0].longitud};
      // this.showAvanceTrain();
    },

    showAvanceTrain() {

      let intevalAdvance = setInterval(() => {
        let temp = JSON.parse(api.showDataTrayecto());
// console.log( temp );
        this.path = temp[0];
        this.markers = temp[1];
      }, 250);
    },

    deployInfoWindows() {
      this.infoWinOpen_LessInfo = false;
    },

    toggleInfoWindow: function(marker, idx) {
      /**
       * Queda pendiente el detalle de reiniciar las variables a su estado original para que se muestren las "infoWindows" en el orden correcto.
       */

      this.infoWindowPos = marker.position;
      this.infoContent = this.getInfoWindowContent(marker);
      this.infoContent_LessInfo = this.getInfoWindowContentLessInfo(marker);

      //check if its the same marker that was selected if yes toggle
      if (
        /* this.currentMidx == idx && this.infoWinOpen && !this.infoWinOpen_LessInfo || */ this
          .currentMidx == idx &&
        !this.infoWinOpen &&
        this.infoWinOpen_LessInfo
      ) {
        this.infoWinOpen = !this.infoWinOpen;
        this.infoWinOpen_LessInfo = !this.infoWinOpen_LessInfo;
      } else if (
        this.currentMidx == idx &&
        this.infoWinOpen &&
        !this.infoWinOpen_LessInfo
      ) {
        this.infoWinOpen = false;
        this.infoWinOpen_LessInfo = true;
      }

      //if different marker set infowindow to open and reset current marker index
      else {
        this.infoWinOpen = false;
        this.infoWinOpen_LessInfo = true;
        this.currentMidx = idx;
      }
    },
    getInfoWindowContent: function(marker) {
      return `<div class="">
                      <div>
                        <p class="title is-4">${marker.locomotora}</p>
                        <hr>
                      </div>

                    <div class="content">

                      <p class="cardContent"><b>Velocidad:</b> ${marker.velocidad} </p>
                      <p class="cardContent"><b>Kilometraje:</b> ${marker.kilometraje} </p>
                      <p class="cardContent"><b>Voltajes:</b> ${marker.voltajes} </p>
                      <p class="cardContent"><b>Direccion:</b> ${marker.direccion} </p>
                      <p class="cardContent"><b>Motor:</b> ${marker.motor} </p>
                      <p class="cardContent"><b>Vel. Engranada:</b> ${marker.engranaje} </p>
                      <p class="cardContent"><b>Nvl. Diesel (lts):</b> ${marker.lts_combustible} </p>
                      <p class="cardContent"><b>Nvl. Diesel (%):</b> ${marker.n_combustible} </p>
                      <p class="cardContent"><b>Geoposicion:</b> lat: ${marker.position["lat"]}, lng: ${marker.position["lng"]} </p>

                      <b>Fecha:</b>
                      <time datetime="2016-1-1">${marker.fecha}</time>

                    </div>
                </div>`;
    },

    getInfoWindowContentLessInfo: function(marker) {
      return `<div class="">
                      <div>
                        <p class="title is-4">${marker.locomotora}</p>
                        <hr>
                      </div>

                    <div class="content">
                      <p class="cardContent"><b>Velocidad:</b> ${marker.velocidad} </p>
                      <p class="cardContent"><b>Nvl. Diesel (%):</b> ${marker.lts_combustible} </p>
                      <p class="cardContent"><b>Nvl. Diesel (%):</b> ${marker.n_combustible} </p>
                    </div>
                </div>`;
    }
  },
  mounted() {

    let login = localStorage.getItem("login_token");
    if (!login) {
      this.$router.push("/");
    }

    api.getLocomotoras();
  }
};
</script>

<style>
#map {
  min-height: calc(100vh - 123px);
}
.cardContent {
  font-family: "Courier New";
  font-size: 1.062em;
}
</style>
